@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(./fonts/MaterialIcons-Regular.eot);
  /* For IE6-8 */
  src: local("Material Icons"), local("MaterialIcons-Regular"), url(./fonts/MaterialIcons-Regular.woff2) format("woff2"), url(./fonts/MaterialIcons-Regular.woff) format("woff"), url(./fonts/MaterialIcons-Regular.ttf) format("truetype"); }

html {
  font-size: 10px;
  min-height: 100%; }

body {
  overflow: auto;
  min-height: 100%;
  min-width: 320px;
  font-weight: normal;
  font-size: 1.6rem;
  line-height: 2.4rem;
  color: #3b4256;
  box-sizing: border-box; }

.page-locked {
  overflow: hidden;
  height: 100vh; }

.hidden {
  display: none !important; }

.material-icons {
  font-family: 'Material Icons' !important;
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  width: 1em;
  height: 1em;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: 'liga'; }

.btn {
  line-height: 2.4rem;
  min-width: 112px;
  display: inline-block;
  border-radius: 4px;
  background: #FFF;
  padding: 12px 15px;
  color: #3b4256 !important;
  font-weight: 500;
  text-align: center;
  border: none;
  transition: all 0.2s linear 0s; }

.btn.btn__secondary {
  min-height: 150px;
  border: 1px solid #ffffff;
  border-radius: 75px;
  background: #ffffff;
  color: #000000 !important;
  font-size: 5rem;
  font-weight: bold;
  line-height: 6rem;
  box-shadow: 2px 2px 10px rgba(204, 204, 204, 0.8);
  outline: none; }
  @media (max-width: 767px) {
    .btn.btn__secondary {
      min-height: auto;
      font-size: 3rem;
      line-height: 4rem; } }

.btn.btn__secondary:hover {
  box-shadow: 2px 2px 10px rgba(121, 146, 228, 0.5); }

h2 {
  font-size: 2.2rem;
  line-height: 3rem;
  font-weight: bold;
  letter-spacing: -0.4px;
  margin-bottom: 22px;
  margin-top: 22px;
  font-family: 'Roboto' !important; }

@media screen and (min-width: 768px) {
  h2 {
    font-size: 3.2rem;
    line-height: 4rem; } }

.interactive-video {
  min-width: 100%; }
  .interactive-video__video {
    display: block;
    max-height: 100vh;
    width: 100%; }
  .interactive-video__question {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url("https://raw.githubusercontent.com/bbrvi2008/interactive-video/master/src/files/questions/background.svg"); }
  .interactive-video__question-title {
    color: #FFF;
    text-align: center;
    padding: 0 22px;
    margin-bottom: 0; }
  .interactive-video__answer-list {
    display: none;
    align-items: center;
    flex-wrap: nowrap;
    background-image: url("https://raw.githubusercontent.com/bbrvi2008/interactive-video/maxsoft-video/src/img/background_1.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center; }
    @media (max-width: 767px) {
      .interactive-video__answer-list {
        justify-content: center;
        align-items: unset;
        flex-wrap: wrap; } }
    @media (orientation: landscape), (min-width: 768px) {
      .interactive-video__answer-list {
        display: flex;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        padding: 30px; } }
  .interactive-video__answer-item {
    flex: 0 0 100%;
    margin: 30px; }
    @media (min-width: 768px) {
      .interactive-video__answer-item {
        flex-shrink: 1; } }
  .interactive-video__inner {
    position: relative;
    background: rgba(30, 30, 30, 0.6);
    box-shadow: 2px 2px 10px rgba(30, 30, 30, 0.6);
    min-width: 100%; }
    .interactive-video__inner::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-image: url("https://raw.githubusercontent.com/bbrvi2008/interactive-video/maxsoft-video/src/img/background_2.jpg");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center; }
    @media (orientation: portrait) and (max-width: 767px) {
      .interactive-video__inner:before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: #3b4256;
        background-image: none;
        z-index: 10; } }
  .interactive-video__inner--video-playing::before {
    display: none; }
  .interactive-video__btn-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 100px;
    color: #ccc;
    background: rgba(30, 30, 30, 0.6);
    border-radius: 50%;
    cursor: pointer;
    z-index: 9; }
  .interactive-video__overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    text-align: center; }
    @media (orientation: landscape), (min-width: 768px) {
      .interactive-video__overlay {
        display: none; } }
  .interactive-video__overlay-icon {
    font-size: 100px;
    color: #ccc;
    cursor: pointer; }
  .interactive-video__overlay-text {
    color: #ccc;
    font-size: 22px;
    margin-bottom: 0; }
  .interactive-video__btn-close {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 48px;
    color: #ccc;
    background: rgba(30, 30, 30, 0.6);
    cursor: pointer;
    z-index: 9; }
  .interactive-video__info {
    padding: 32px 32px;
    color: #FFF;
    font-size: 48px;
    line-height: 58px;
    font-weight: bold;
    text-align: center;
    -webkit-filter: drop-shadow(2px 2px 2px rgba(30, 30, 30, 0.6));
            filter: drop-shadow(2px 2px 2px rgba(30, 30, 30, 0.6)); }
  .interactive-video--fullscreen {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: rgba(59, 66, 86, 0.97);
    transition-timing-function: cubic-bezier(0.22, 0.61, 0.36, 1); }
    .interactive-video--fullscreen .interactive-video__btn-play {
      display: none; }
    .interactive-video--fullscreen .interactive-video__btn-close {
      display: block; }

.view-console {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 10px;
  color: #FFF;
  z-index: 100; }

.interactive-video-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  background: rgba(59, 66, 86, 0.97); }
